.rmd-link--wrapper {
  display: inline-block !important;
  position: relative; }
  .rmd-link--wrapper .rmd-tooltip--wrapper {
    display: inline; }

.rmd-tooltip--container .rmd-link:not([href]) {
  border-bottom: 1px dashed #333;
  cursor: default; }

.rmd-link--tooltip.rmd-tooltip {
  bottom: calc(100% + .25rem); }
  .rmd-link--tooltip.rmd-tooltip__bottom {
    top: calc(100% + .5rem);
    bottom: auto; }
  .rmd-link--tooltip.rmd-tooltip__right {
    left: calc(100% + .5rem);
    bottom: 50%; }
  .rmd-link--tooltip.rmd-tooltip__left {
    right: calc(100% + .5rem);
    bottom: 50%; }



